body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
   background-image: -webkit-radial-gradient(rgb(78, 158, 250) 5%, rgb(119, 227, 241) 15%, rgb(190, 250, 193) 60%);
   background-image: radial-gradient(rgb(78, 158, 250) 5%, rgb(119, 227, 241) 15%, rgb(190, 250, 193) 60%);
padding-bottom: 100px;
  }

.input{
  font-size: 4em !important;
  height: 60px !important;
  margin-top: 10px;
  text-align: center;
}

.clear{
  margin-top: 10px;
    font-size: 2em !important;
    width: 100%;
    color: darkorange !important;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.clear:hover{
  color: blueviolet !important;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.boutons{
    font-size: 4em !important;
    width: 100%;
    color: darkorange !important;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.ligne{
    margin-top:20px;
}

.boutons:hover{
    color: blueviolet !important;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    
}
.History{
    margin-top: 50px;
    text-align: center;
    padding-bottom: 50px;
}
.zoom{
    -webkit-transition: 2s;
    transition: 2s;
    color: rgb(199, 199, 199);
     background-color: rgba(255, 255, 255, 0);
}

 .zoom:hover{
    font-size: 5em;
    -webkit-transition: 2s;
    transition: 2s;
    color: black;
   background-color: rgba(106, 255, 255, 0.842);
   border-radius: 1000px;
}

li{
    list-style: none;
}

